<template>
  <view class="main-container">
    <!-- ÂÜÖÂÆπÂå∫Âüü -->
    <view ref="tabView" class="tab-view">
      <!-- ÊÄßËÉΩ‰ºòÂåñÔºöÈ¶ñÊ¨°‰ΩøÁî®v-ifÂàõÂª∫ÔºåÂêéÁª≠‰ΩøÁî®visibilityÊéßÂà∂ -->
      <!-- È¶ñÈ°µ -->
      <view 
        class="tab-page"
        v-if="tabList[0].init"
        :style="{ visibility: selectedIndex === 0 ? 'visible' : 'hidden' }"
      >
        <IndexPage 
          ref="tab-0"
          @scroll="onPageScroll"
        />
      </view>
      
      <!-- ÁÉ≠ÁÇπÈ°µ -->
      <view 
        class="tab-page"
        v-if="tabList[1].init"
        :style="{ visibility: selectedIndex === 1 ? 'visible' : 'hidden' }"
      >
        <CategoryPage 
          ref="tab-1"
          @scroll="onPageScroll"
        />
      </view>
      
      <!-- ËÅäÂ§©È°µ -->
      <view 
        class="tab-page"
        v-if="tabList[2].init"
        :style="{ visibility: selectedIndex === 2 ? 'visible' : 'hidden' }"
      >
        <CartPage 
          ref="tab-2"
          @scroll="onPageScroll"
        />
      </view>
      
      <!-- ÊàëÁöÑÈ°µ -->
      <view 
        class="tab-page"
        v-if="tabList[3].init"
        :style="{ visibility: selectedIndex === 3 ? 'visible' : 'hidden' }"
      >
        <UserPage 
          ref="tab-3"
          @scroll="onPageScroll"
        />
      </view>
    </view>
    
    <!-- Ëá™ÂÆö‰πâÂ∫ïÈÉ®ÂØºËà™Ê†è -->
    <view ref="tabbar" class="custom-tabbar">
      <!-- ÂØºËà™È°πÂÆπÂô® -->
      <view class="tabbar-content">
        <!-- ÊâÄÊúâÂØºËà™È°π -->
        <view 
          v-for="(item, index) in tabConfig" 
          :key="index"
          class="tab-item"
          :class="{ active: selectedIndex === index }"
          @tap="onTabClick(index)"
        >
          <!-- ÁâπÊÆäÁöÑAIÂõæÊ†á (AiÊàë) -->
          <view 
            v-if="index === 0 && item.isSpecial" 
            class="ai-special-container"
          >
            <!-- ÊªöÂä®ÁÆ≠Â§¥ -->
            <text 
              v-if="showArrow"
              class="tab-icon arrow-icon"
              :class="{ active: selectedIndex === index }"
            >
              ‚¨ÜÔ∏è
            </text>
            <!-- AIÂõæÊ†áÂÆπÂô® -->
            <view v-else class="ai-icon-container" :class="{ active: selectedIndex === index }">
              <!-- ÈÄâ‰∏≠Êó∂ÊòæÁ§∫GIF -->
              <image 
                v-if="selectedIndex === index"
                class="ai-gif-icon"
                :src="item.selectedIconPath"
                mode="aspectFit"
              />
              <!-- Êú™ÈÄâ‰∏≠Êó∂ÊòæÁ§∫PNG -->
              <image 
                v-else
                class="ai-png-icon"
                :src="item.iconPath"
                mode="aspectFit"
              />
            </view>
            <!-- ÊñáÂ≠óÊ†áÁ≠æ - Âè™Âú®Êú™ÈÄâ‰∏≠Êó∂ÊòæÁ§∫ -->
            <text 
              v-if="!showArrow && selectedIndex !== index"
              class="tab-text"
              :class="{ active: selectedIndex === index }"
            >
              {{ item.text }}
            </text>
          </view>
          
          <!-- ÂÖ∂‰ªñÊôÆÈÄöÂõæÊ†á -->
          <template v-else>
            <!-- ÂõæÊ†áÂÆπÂô® -->
            <view class="tab-icon-container">
              <image 
                class="tab-icon-image"
                :src="getIconSrc(item, index)"
                mode="aspectFit"
                :class="{ active: selectedIndex === index }"
                :key="`icon-${index}-${gifPlayStatus[index] || 'static'}`"
              />
            </view>
            <!-- ÊñáÂ≠óÊ†áÁ≠æ -->
            <text 
              class="tab-text"
              :class="{ active: selectedIndex === index }"
            >
              {{ item.text }}
            </text>
          </template>
        </view>
      </view>
      
      
    </view>
  </view>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted, nextTick } from 'vue'

// ÂØºÂÖ•È°µÈù¢ÁªÑ‰ª∂
import IndexPage from '@/pages/index/index.vue'
import CategoryPage from '@/pages/category/index.vue'
import CartPage from '@/pages/cart/index.vue'
import UserPage from '@/pages/user/index.vue'

// ÂìçÂ∫îÂºèÊï∞ÊçÆ
const selectedIndex = ref(0)
const showArrow = ref(false)
const tabViewHeight = ref(0)
const lastScrollTop = ref(0)
const gifPlayStatus = ref({}) // ËÆ∞ÂΩïÊØè‰∏™tabÁöÑGIFÊí≠ÊîæÁä∂ÊÄÅ

// TabÈÖçÁΩÆ - ÂæÆ‰ø°Â∞èÁ®ãÂ∫èÂÖºÂÆπÁâàÊú¨
const tabConfig = [
  { 
    iconPath: '/static/tabbar/ai.png',
    selectedIconPath: '/static/icons/ai-robot.gif', // AI‰ΩøÁî®GIF
    text: 'AiÊàë',
    init: true,
    isSpecial: true  // Ê†áËÆ∞‰∏∫ÁâπÊÆäÂõæÊ†á
  },
  { 
    iconPath: '/static/tabbar/hotspot.png',
    selectedIconPath: '/static/icons/hotspot.gif', // ÊÅ¢Â§ç‰ΩøÁî®GIFÔºåÁ°Æ‰øùÊòæÁ§∫Ê≠£Â∏∏
    selectedIconEndFrame: '/static/icons/hotspot-end.png', // ÊúÄÂêé‰∏ÄÂ∏ßÈùôÊÄÅÂõæ
    text: 'ÁÉ≠ÁÇπ',
    init: false,
    gifDuration: 240
  },
  { 
    iconPath: '/static/tabbar/chat.png',
    selectedIconPath: '/static/icons/chat.gif', // ÊÅ¢Â§ç‰ΩøÁî®GIFÔºåÁ°Æ‰øùÊòæÁ§∫Ê≠£Â∏∏
    selectedIconEndFrame: '/static/icons/chat-end.png', // ÊúÄÂêé‰∏ÄÂ∏ßÈùôÊÄÅÂõæ
    text: 'ËÅäÂ§©',
    init: false,
    gifDuration: 240
  },
  { 
    iconPath: '/static/tabbar/user.png',
    selectedIconPath: '/static/icons/user.gif', // ÊÅ¢Â§ç‰ΩøÁî®GIFÔºåÁ°Æ‰øùÊòæÁ§∫Ê≠£Â∏∏
    selectedIconEndFrame: '/static/icons/user-end.png', // ÊúÄÂêé‰∏ÄÂ∏ßÈùôÊÄÅÂõæ
    text: 'ÊàëÁöÑ',
    init: false,
    gifDuration: 240
  }
]

const tabList = ref(tabConfig)

// Ëé∑ÂèñÂõæÊ†áÊ∫êË∑ØÂæÑ
const getIconSrc = (item, index) => {
  if (selectedIndex.value !== index) {
    // Êú™ÈÄâ‰∏≠Êó∂ÊòæÁ§∫ÊôÆÈÄöÂõæÊ†á
    return item.iconPath
  }
  
  // ÈÄâ‰∏≠Êó∂ÁöÑÈÄªËæë
  if (index === 0) {
    // AIÁâπÊÆäÂ§ÑÁêÜÔºåÁõ¥Êé•ËøîÂõûGIF
    return item.selectedIconPath
  }
  
  // ÂÖ∂‰ªñtabÁöÑGIFÊéßÂà∂ÈÄªËæë
  const playStatus = gifPlayStatus.value[index]
  if (playStatus === 'playing') {
    return item.selectedIconPath // ÊòæÁ§∫GIF
  } else if (playStatus === 'ended') {
    return item.selectedIconEndFrame || item.selectedIconPath // ÊòæÁ§∫ÊúÄÂêé‰∏ÄÂ∏ßÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàôÁªßÁª≠ÊòæÁ§∫GIF
  } else {
    return item.selectedIconPath // È¶ñÊ¨°ÈÄâ‰∏≠ÊòæÁ§∫GIF
  }
}

// Â§ÑÁêÜGIFÊí≠Êîæ
const handleGifPlayback = (index) => {
  const config = tabConfig[index]
  
  // ÊØèÊ¨°ÂàáÊç¢ÈÉΩÈáçÊñ∞Êí≠ÊîæGIF
  // ÂºÄÂßãÊí≠ÊîæGIF
  gifPlayStatus.value[index] = 'playing'
  
  // ‰ΩøÁî®ÈÖçÁΩÆ‰∏≠ÁöÑËá™ÂÆö‰πâÊí≠ÊîæÊó∂Èïø
  const duration = config.gifDuration || 1000 // ÈªòËÆ§1Áßí
  
  console.log(`üé¨ ${config.text} GIFÂºÄÂßãÊí≠ÊîæÔºåÊó∂Èïø: ${duration}ms`)
  
  // Âè™ÊúâÈÖçÁΩÆ‰∫ÜÊúÄÂêé‰∏ÄÂ∏ßÂõæÁâáÊâçËøõË°åÂàáÊç¢
  if (config.selectedIconEndFrame) {
    // Êí≠ÊîæÂÆåÊàêÂêéÂàáÊç¢Âà∞ÊúÄÂêé‰∏ÄÂ∏ß
    setTimeout(() => {
      gifPlayStatus.value[index] = 'ended'
      console.log(`üéØ ${config.text} GIFÊí≠ÊîæÂÆåÊàêÔºåÊòæÁ§∫ÊúÄÂêé‰∏ÄÂ∏ß`)
    }, duration)
  }
}

// ÈáçÁΩÆÊâÄÊúâGIFÊí≠ÊîæÁä∂ÊÄÅ
const resetAllGifStatus = () => {
  gifPlayStatus.value = {}
  console.log('üîÑ ÊâÄÊúâGIFÊí≠ÊîæÁä∂ÊÄÅÂ∑≤ÈáçÁΩÆ')
}



// ÁîüÂëΩÂë®Êúü
onMounted(() => {
  initContainer()
  // ÁõëÂê¨È°µÈù¢ÊªöÂä®‰∫ã‰ª∂
  uni.$on('pageScroll', onPageScrollEvent)
})

onUnmounted(() => {
  uni.$off('pageScroll', onPageScrollEvent)
})

// ÂàùÂßãÂåñÂÆπÂô®
const initContainer = async () => {
  await nextTick()
  // Ëé∑ÂèñÂÆπÂô®È´òÂ∫¶Áî®‰∫éÂà§Êñ≠ÊòØÂê¶ÊòæÁ§∫ÁÆ≠Â§¥
  uni.createSelectorQuery().in(this).select('.tab-view').boundingClientRect((data) => {
    if (data) {
      tabViewHeight.value = data.height
    }
  }).exec()
}

// TabÁÇπÂáªÂ§ÑÁêÜ
const onTabClick = (index) => {
  console.log(`üîÑ ÁÇπÂáªTab: ${index}`)
  
  // Â¶ÇÊûúÁÇπÂáªÁöÑÊòØÂΩìÂâçÈ¶ñÈ°µ‰∏îÊòæÁ§∫ÁÆ≠Â§¥ÔºåÂàôÂõûÂà∞È°∂ÈÉ®
  if (selectedIndex.value === index && index === 0 && showArrow.value) {
    scrollToTop()
    return
  }
  
  // Â¶ÇÊûúÁÇπÂáªÁöÑÊòØÂêå‰∏Ä‰∏™tabÔºå‰∏çÈúÄË¶ÅÂàáÊç¢
  if (selectedIndex.value === index) {
    return
  }
  
  const previousIndex = selectedIndex.value
  
  // Â¶ÇÊûúÂàáÊç¢Âà∞ÈùûÈ¶ñÈ°µÔºåÈöêËóèÁÆ≠Â§¥
  if (index !== 0) {
    showArrow.value = false
  } else if (index === 0 && selectedIndex.value !== 0) {
    // ÂàáÂõûÈ¶ñÈ°µÊó∂ÔºåÊ†πÊçÆ‰πãÂâçÁöÑÊªöÂä®‰ΩçÁΩÆÂÜ≥ÂÆöÊòØÂê¶ÊòæÁ§∫ÁÆ≠Â§¥
    showArrow.value = lastScrollTop.value > tabViewHeight.value
  }
  
  // ÈáçÁΩÆÂâç‰∏Ä‰∏™tabÁöÑGIFÁä∂ÊÄÅÔºàÈô§‰∫ÜAI tabÔºâ
  if (previousIndex !== 0 && tabConfig[previousIndex].selectedIconPath.includes('.gif')) {
    delete gifPlayStatus.value[previousIndex]
  }
  
  // ÊáíÂä†ËΩΩÔºöÈ¶ñÊ¨°ÁÇπÂáªÊó∂ÂàùÂßãÂåñÈ°µÈù¢
  if (!tabList.value[index].init) {
    tabList.value[index].init = true
  }
  
  selectedIndex.value = index
  
  // ÊéßÂà∂GIFÊí≠ÊîæÔºàÈô§‰∫ÜAI tabÔºâ
  if (index !== 0 && tabConfig[index].selectedIconPath.includes('.gif')) {
    handleGifPlayback(index)
  }
}



// È°µÈù¢ÊªöÂä®‰∫ã‰ª∂Â§ÑÁêÜ
const onPageScrollEvent = (scrollTop) => {
  if (selectedIndex.value === 0) { // Âè™ÊúâÈ¶ñÈ°µÊâçÂ§ÑÁêÜÊªöÂä®
    showArrow.value = scrollTop > tabViewHeight.value
    lastScrollTop.value = scrollTop
  }
}

// ÊªöÂä®Âà∞È°∂ÈÉ®
const scrollToTop = () => {
  showArrow.value = false
  // ÈÄöÁü•È¶ñÈ°µÊªöÂä®Âà∞È°∂ÈÉ®
  const tab0 = this.$refs['tab-0']
  if (tab0 && tab0[0] && typeof tab0[0].scrollToTop === 'function') {
    tab0[0].scrollToTop()
  }
}


// È°µÈù¢ÊªöÂä®ÂõûË∞É
const onPageScroll = (event) => {
  if (selectedIndex.value === 0) {
    onPageScrollEvent(event.detail?.scrollTop || 0)
  }
}
</script>

<style scoped>
/* ÂØºÂÖ•ÂõæÊ†áÂ≠ó‰Ωì */
@import url('/static/fonts/uni-icon.css');

