  background-color: #fff;
}

/* Ëá™ÂÆö‰πâÂ∫ïÈÉ®ÂØºËà™Ê†è */
.custom-tabbar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 160rpx;
  z-index: 1000;
  background-color: #ffffff;
  border-top: 1rpx solid #e5e5e5;
  box-shadow: 0 -4rpx 12rpx rgba(0, 0, 0, 0.1);
  /* #ifdef APP-PLUS || MP */
  padding-bottom: env(safe-area-inset-bottom);
  /* #endif */
}

/* ÂØºËà™ÂÜÖÂÆπ */
.tabbar-content {
  width: 100%;
  height: 160rpx;
  display: flex;
  align-items: center;
  padding: 0 20rpx;
}

/* ÂØºËà™È°π */
.tab-item {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 10rpx 10rpx 10rpx 10rpx;
  transform: translateY(0);
  min-height: 120rpx;
}

.tab-item.active {
  transform: translateY(0);
}

/* ÂõæÊ†áÂÆπÂô® */
.tab-icon-container {
  position: relative;
  margin-bottom: 4rpx;
  height: 60rpx;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tab-item.active .tab-icon-container {
  transform: scale(1);
}

/* ÂõæÊ†áÊ†∑Âºè */
.tab-icon {
  font-size: 44rpx;
  color: #999;
  display: inline-block;
}

.tab-icon.active {
  color: #007aff;
}

.arrow-icon {
  font-size: 60rpx !important;
  font-weight: bold;
  color: #ff4757 !important;
}

/* PNGÂõæÊ†áÊ†∑Âºè */
.tab-icon-image {
  width: 44rpx;
  height: 44rpx;
  transition: all 0.1s ease-out; /* Ê∑ªÂä†Âπ≥ÊªëËøáÊ∏° */
}

.tab-icon-image.active {
  transform: scale(1);
}

/* ÁâπÊÆäÁöÑAIÂõæÊ†áÊ†∑Âºè */
.ai-special-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
}

.ai-icon-container {
  position: relative;
  margin-bottom: 4rpx;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Êú™ÈÄâ‰∏≠Êó∂ÁöÑAIÂõæÊ†áÂÆπÂô® - Â∞èÂ∞∫ÂØ∏ */
.ai-icon-container:not(.active) {
  height: 60rpx;
  width: auto;
}

/* ÈÄâ‰∏≠Êó∂ÁöÑAIÂõæÊ†áÂÆπÂô® - Â§ßÂ∞∫ÂØ∏ */
.ai-icon-container.active {
  height: 100rpx;
  width: 140rpx;
}

.ai-icon-container.active {
  transform: scale(1);
}

/* ÈÄâ‰∏≠Êó∂ÁöÑGIFÂõæÊ†á - Â§ßÂ∞∫ÂØ∏ÔºåÈì∫Êª°ÂÆπÂô® */
.ai-gif-icon {
  width: 100%;
  height: 100%;
  border-radius: 12rpx;
  object-fit: cover;
  transition: opacity 0.1s ease-out; /* Ê∑ªÂä†Âπ≥ÊªëËøáÊ∏° */
}

/* Êú™ÈÄâ‰∏≠Êó∂ÁöÑPNGÂõæÊ†á - ÊôÆÈÄöÂ∞∫ÂØ∏ */
.ai-png-icon {
  width: 44rpx;
  height: 44rpx;
  transition: all 0.1s ease-out; /* Ê∑ªÂä†Âπ≥ÊªëËøáÊ∏° */
}

/* ÂæÆ‰ø°Â∞èÁ®ãÂ∫èÂÖºÂÆπÁöÑÁÆÄÂçïÂä®ÁîªÊïàÊûú */
.tab-icon-image.active {
  animation: simpleScale 0.2s ease-in-out;
}

@keyframes simpleScale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

/* ÊñáÂ≠óÊ†∑Âºè */
.tab-text {
  font-size: 16rpx;
  color: #999;
  text-align: center;
  margin-top: 2rpx;
}

.tab-text.active {
  color: #ff4757;
  font-weight: 500;
}





/* H5Á´ØÈÄÇÈÖç */
/* #ifdef H5 */
.custom-tabbar {
  height: 80px;
}

