}

/* Êú™ÈÄâ‰∏≠Êó∂ÁöÑAIÂõæÊ†áÂÆπÂô® - Â∞èÂ∞∫ÂØ∏ */
.ai-icon-container:not(.active) {
  margin-bottom: 2px;
  height: 30px;
